import React from "react";
import { Container, Row, Col, Accordion } from "react-bootstrap";
import { StaticImage } from "gatsby-plugin-image";

const Faq = () => {
  return (
    <div className="faq_outline">
      <Container>
        <Row className="faq_row">
          <Col xxl={6} xl={6} md={6} sm={12} className="d-flex align-items-end">
            <div className="faq_lhs">
              <StaticImage
                src="../../styles/assets/images/recentworks/faq.png"
                alt="Frequently asked questions poster"
              />
            </div>
          </Col>
          <Col xxl={6} xl={6} md={6} sm={12}>
            <div className="faq_rhs">
              <h1>Resolve your unanswered queries effortlessly</h1>
              <div className="faq_acc_wrapper">
                <Accordion>
                  <Accordion.Item eventKey="0">
                    <Accordion.Header>
                      Why Justtry for white-label crypto exchange development?
                    </Accordion.Header>
                    <Accordion.Body>
                      A customizable dashboard for users to manage their
                      profiles and monitor trading activities on the go.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item eventKey="1">
                    <Accordion.Header>
                      Launch your white-label crypto exchange in a week?
                    </Accordion.Header>
                    <Accordion.Body>
                      Start in a week with our Experts. Choose: Develop from
                      scratch or white-label your crypto exchange software.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item eventKey="2">
                    <Accordion.Header>
                      Ensure your crypto exchange's reliability?
                    </Accordion.Header>
                    <Accordion.Body>
                      Enhance exchange security with features like two-factor
                      authentication, SSL Implementation, encrypted user access,
                      and automated withdrawal limits.
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item eventKey="3">
                    <Accordion.Header>
                      Ensure your crypto exchange's reliability?
                    </Accordion.Header>
                    <Accordion.Body>
                      Enhance exchange security with features like two-factor
                      authentication, SSL Implementation, encrypted user access,
                      and automated withdrawal limits.
                    </Accordion.Body>
                  </Accordion.Item>
                </Accordion>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Faq;
